import React from "react";
import { NavLink } from "react-router-dom";
import "../Form.css";
import {IconWrapperStyledComponent,
    Input,
    CheckBoxSC,
    ConfirmButtonSC,
    CopyrightParagraphSC} from "../styledForms";

export default class RegisterForm extends React.Component{
    state = {
        firstName: "",
        lastName: "",
        email: "",
        password: "",
        subscribe: false,
      };
      saveInput = (e) => {
        this.setState({ [e.target.name]: e.target.value });
      };
      rememberMeToggle = (e) => {
        this.setState({ [e.target.name]: e.target.checked });
      };
      onSubmit = () => {
        console.log(this.state);
        alert("Done");
      };

    render(){
        return(
            <>
            <div className="wrapper">
                <IconWrapperStyledComponent>                        
                    <img className="icon" src="data:image/svg+xml;base64," />
                </IconWrapperStyledComponent>
                <p>Sign Up</p>
                <span className="inputName" >
                    <Input placeholder="First Name*" onChange={this.saveInput} ></Input>
                    <Input placeholder="Last Name*" onChange={this.saveInput}></Input>
                </span>
                <Input placeholder="Email Address*" onChange={this.saveInput}></Input>
                <Input placeholder="Password*" onChange={this.saveInput}></Input>
                <label className="checkBoxLabel">
                     <CheckBoxSC type="checkbox" onChange={this.rememberMeToggle} ></CheckBoxSC>
                        I want tu receive inspiration,marketing promotions and updates via email
                </label>
                <ConfirmButtonSC onClick={this.onSubmit}>Sign Up</ConfirmButtonSC>
                <span className="NavLinkSpanRegister">                    
                    <NavLink className="navLink" to="/enterForm">Already have an account? Sign in</NavLink>
                </span>

                <CopyrightParagraphSC>Copyright © Your Website 2020.</CopyrightParagraphSC>
            </div>
            </>
        )
    }
}